import Container from "react-bootstrap/Container";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
import logo from "../../images/logo/logo.png";
import { Button } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import "./navbar.css";
import { useWeb3React } from '@web3-react/core';
import { WalletConnectConnector } from '@web3-react/walletconnect-connector';
import { InjectedConnector } from '@web3-react/injected-connector';

const walletconnect = new WalletConnectConnector({
  rpc: { 1: "https://mainnet.infura.io/v3/3a137aaa56ac43518c4aca7dc9fcbbd7" },
  bridge: "https://bridge.walletconnect.org",
  qrcode: true,
});

const injected = new InjectedConnector({});

function NavBar() {
  const { activate, active, account, deactivate } = useWeb3React();

  const connectWallet = async (connector) => {
    try {
      await activate(connector);
    } catch (error) {
      console.error("Connection failed:", error);
    }
  };

  const handleConnect = () => {
    // Replace with your preferred connector, e.g., walletconnect or injected
    connectWallet(walletconnect);
  };

  const handleDisconnect = () => {
    deactivate();
  };

  return (
    <Navbar expand="lg" className="py-3">
      <Container>
        <Navbar.Brand href="#" className="me-lg-5">
          <img className="logo" src={logo} alt="logo" />
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="navbarScroll" />
        <Navbar.Collapse id="navbarScroll">
          <Nav className="me-auto my-2 my-lg-0" navbarScroll>
            <Nav.Link href="#action1">Marketplace</Nav.Link>
            <Nav.Link href="#action2" className="px-lg-3">
              About Us
            </Nav.Link>
            <Nav.Link href="#action3">Developers</Nav.Link>
          </Nav>
        </Navbar.Collapse>
        <div className="d-flex align-items-center order">
          <span className="line d-lg-inline-block d-none"></span>
          <i className="fa-regular fa-heart"></i>
          {active ? (
            <Button
              variant="primary"
              className="btn-primary d-none d-lg-inline-block"
              onClick={handleDisconnect}
            >
              Disconnect {account.slice(0, 6) + '...' + account.slice(-4)}
            </Button>
          ) : (
            <Button
              variant="primary"
              className="btn-primary d-none d-lg-inline-block"
              onClick={handleConnect}
            >
              Connect Wallet
            </Button>
          )}
        </div>
      </Container>
    </Navbar>
  );
}

export default NavBar;
